html, body {
  display: flex;
  width: 100%;
  height: 100%;
}

body {
  flex: 1;
  background: linear-gradient(180deg, hsl(235, 16%, 14%) 0%, hsl(236, 21%, 26%) 100%);
}

div {
  display: flex;
}

body h1 {
  flex: 1;
  justify-content: center;
}

.countdown {
  flex: 1;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.day, .hour, .minute, .second {
  width: 200px;
  height: 200px;
  margin: 25px;
  background-color: hsl(234, 17%, 12%);
  flex-direction: column;
  border-radius: 20px;
  overflow: hidden;
  box-shadow: 0 10px 0 black;
}

.upper {
  flex: 1;
  max-height: 100px;
  border-radius: 20px 20px 0 0;
  align-items: center;
  text-align: center;
  background-color: darken(hsl(236, 21%, 26%), 5%);
  //border-bottom: 1px solid black;
  //background: linear-gradient(180deg,hsl(235, 16%, 14%) 0, hsl(235, 16%, 14%) 50%, hsl(236, 21%, 26%) 50%, hsl(236, 21%, 26%) 100%);

  &:before {
    content: "";
    align-self: flex-end;
    background-color: black;
    position: relative;
    right: 10px;
    height: 20px;
    width: 20px;
    top: 10px;
    border-radius: 10px;
    z-index: 1;
  }

  & .number--container {
    overflow: hidden;
    flex: 1;
    margin-right: 20px;
  }

  & .number {
    color: black;//darken(hsl(345, 95%, 68%), 5%);
    padding-top: 50%;
  }
}

.lower {
  flex: 1;
  flex-direction: row-reverse;
  max-height: 100px;
  border-radius: 0 0 20px 20px;
  align-items: center;
  text-align: center;
  //border-top: 1px solid black;
  background-color: hsl(236, 21%, 26%);

  &:before {
    content: "";
    align-self: flex-start;
    background-color: black;
    position: relative;
    right: -10px;
    height: 20px;
    width: 20px;
    bottom: 10px;
    border-radius: 10px;
    z-index: 1;
  }

  & .number--container {
    overflow: hidden;
    flex: 1;
    margin-left: 20px;
  }

  & .number {
    //margin-left: -16.5px;
    //margin-bottom: 50%;
    color: hsl(345, 95%, 68%);
    padding-bottom: 50%;
  }
}

.number--container {
  //max-height: 100%;
}

.number {
  font-size: 120px;
  width: 100%;
  margin: 0;
}

p {
  color: white;
  @font-face {
    font-family: Red Hat Text;
    src: url('https://fonts.google.com/specimen/Red+Hat+Text');
  }
  //font-size: 14px;
}
